// Vue.js ile birlikte kullanƒ±lan Vuelidate k√ºt√ºphanesi yardƒ±mƒ±yla bir form doƒürulamasƒ± olu≈üturulmu≈ütur. 
<template><!-- HTML yapƒ±sƒ±nƒ± tanƒ±mlandƒ±ƒüƒ± b√∂l√ºmd√ºr.-->
  <div class="container">
    <h3 class="text-center mt-3">Vuelidate ile Form Kontrol√º</h3>
    <div class="d-flex justify-content-center align-content-center flex-row">
      <div class="card p-4 mt-3 mr-4 shadow">
        <form style="width: 350px" @submit.prevent="onSubmit">
          <div class="form-group">
            <label>E-posta Adresiniz</label>
            <!--  v-model:  Bu √∂zellik, Vue'de iki y√∂nl√º veri baƒülama (data binding) saƒülar. 
                            Burada, input alanƒ±na girilen deƒüer email deƒüi≈ükenine baƒülanƒ±r.
                  @blur="$v.email.$touch": Input kutusunun dƒ±≈üƒ±na tƒ±klandƒ±ƒüƒ±nda touch metodunu √ßalƒ±≈ütƒ±rarak
                                           ilgili alanƒ±n doƒürulamasƒ±nƒ± ba≈ülatƒ±r.
                  :class="{'is-invalid': $v.email.$error}": Eƒüer email alanƒ±nda bir hata varsa input'a is-invalid sƒ±nƒ±fƒ± eklenir 
                                                            ve bu hata durumu g√∂r√ºn√ºr hale getirilir.
                  v-if: K√º√ß√ºk etiketler small, form hata mesajlarƒ±nƒ± g√∂sterir. 
                        √ñrneƒüin, e-posta zorunlu deƒüilse veya ge√ßersizse, hata mesajƒ± g√∂sterilir.-->
            <input 
              @blur="$v.email.$touch"
              v-model="email" 
              type="email" 
              class="form-control" 
              :class="{'is-invalid':$v.email.$error}"
              placeholder="E-posta adresini giriniz">
            <small v-if="!$v.email.required" class="form-text text-danger">Bu alan zorunludur... </small>
            <small v-if="!$v.email.email" class="form-text text-danger">L√ºtfen Ge√ßerli bir e-posta adresi giriniz... </small>
          </div>
          <!--≈ûifreler i√ßin hem password hem de repassword alanlarƒ± bulunmaktadƒ±r. Bu alanlar belirli kurallara 
          (sayƒ± olma zorunluluƒüu, minLength, maxLength, aynƒ± olma gibi) g√∂re doƒürulanmaktadƒ±r.-->
          <div class="form-group">
            <label>≈ûifre</label>
            <input v-model="$v.password.$model" type="password" class="form-control" placeholder="≈ûifrenizi giriniz">
            <small v-if="!$v.password.required" class="form-text text-danger">Bu alan zorunludur... </small>
            <small v-if="!$v.password.numeric" class="form-text text-danger">L√ºtfen ≈üifreniz rakamlardan olu≈üsun... </small>
            <small v-if="!$v.password.minLength" class="form-text text-danger">L√ºtfen ≈üifreniz en az {{ $v.password.$params.minLength.min }} karakterden olu≈üsun  ... </small>
            <small v-if="!$v.password.maxLength" class="form-text text-danger">L√ºtfen ≈üifreniz en fazla {{ $v.password.$params.maxLength.max }} karakterden olu≈üsun... </small>
          </div>
          <!--v-model="$v.password.$model": password alanƒ±na kullanƒ±cƒ± giri≈üi yapƒ±ldƒ±ƒüƒ±nda 
          vuelidate √ºzerinden doƒürulama yapƒ±lmasƒ±nƒ± saƒülar.-->
          <div class="form-group">
            <label>≈ûifre Tekrar</label>
            <input v-model="$v.repassword.$model" type="password" class="form-control" placeholder="≈ûifrenizi tekrar giriniz">
            <small v-if="!$v.repassword.required" class="form-text text-danger">Bu alan zorunludur... </small>
            <small v-if="!$v.repassword.numeric" class="form-text text-danger">L√ºtfen ≈üifreniz rakamlardan olu≈üsun... </small>
            <small v-if="!$v.repassword.minLength" class="form-text text-danger">L√ºtfen ≈üifreniz en az {{ $v.repassword.$params.minLength.min }} karakterden olu≈üsun  ... </small>
            <small v-if="!$v.repassword.maxLength" class="form-text text-danger">L√ºtfen ≈üifreniz en fazla {{ $v.repassword.$params.maxLength.max }} karakterden olu≈üsun... </small>
            <small v-if="!$v.repassword.sameAs" class="form-text text-danger">Girdiƒüiniz ≈üifreler birbirleriyle uyu≈ümuyor... </small>
          </div>
          <!-- Ya≈ü alanƒ± kullanƒ±cƒ±dan sadece rakamsal giri≈ü kabul eder ve belirli bir aralƒ±kta (18 ile 60) olmalƒ±dƒ±r.-->
          <div class="form-group">
            <label>Ya≈üƒ±nƒ±z</label>
            <input v-model="$v.age.$model" type="text" class="form-control" placeholder="≈ûifrenizi tekrar giriniz">
            <small v-if="!$v.age.required" class="form-text text-danger">Bu alan zorunludur... </small>
            <small v-if="!$v.age.numeric" class="form-text text-danger">L√ºtfen sadece rakam giriniz... </small>
            <small v-if="!$v.age.between" class="form-text text-danger">
              Kayƒ±t olabilmeniz i√ßin ya≈üƒ±nƒ±zƒ±n {{ $v.age.$params.between.min }} ile {{ $v.age.$params.between.max }} arasƒ±nda olmalƒ±dƒ±r... 
            </small>
          </div>
          <!--Kategori alanƒ±, kullanƒ±cƒ±dan bir se√ßim yapƒ±lmasƒ±nƒ± saƒülar ve sadece "Yazƒ±lƒ±m" kategorisi se√ßilebilir.-->
          <div class="form-group">
            <label>Kayƒ±t olmak istediƒüiniz kategori</label>
            <select v-model="$v.selectedCategory.$model" class="form-control">
              <option v-for="category in categories" :key="category" :value="category">{{ category }}</option>
            </select>
            <small v-if="!$v.selectedCategory.checked" class="form-text text-danger">Sadece Yazƒ±lƒ±m Kategorisine ait kayƒ±t olu≈üturabilirsiniz </small>
          </div>
          <!--Kullanƒ±cƒ± ilgi alanlarƒ±nƒ± girebilir ve en az iki ilgi alanƒ± eklemesi gerekmektedir.
           Her ilgi alanƒ± i√ßin minimum 6 karakter giri≈üi gereklidir.-->
          <a @click="newHobby" class="text-white btn btn-secondary rounded-0 btn-sm">ƒ∞lgi Alanƒ± Ekle</a>
          <small v-if="!$v.hobbies.required" class="form-text text-danger">Bu alan zorunludur... </small>
          <small v-if="!$v.hobbies.minLength" class="form-text text-danger">ƒ∞lgi alanlarƒ±nƒ±z en az {{ $v.hobbies.$params.minLength.min }} tane olmalƒ±dƒ±r... </small>
          <ul class="list-group mt-3 mb-3 border-0">
            <li v-for="(hobby, index) in hobbies" :key="hobby.id" class="list-group-item d-flex pl-2">
              <input 
                  type="text" 
                  class="form-control mr-2" 
                  @blur="$v.hobbies.$each[index].value.$touch()"
                  :class="{'is-invalid' : $v.hobbies.$each[index].$error}"
                  v-model="hobby.value">
              <button class="btn btn-sm btn-danger rounded-0" @click="hobbies.splice(index, 1)">Sil</button>
            </li>
          </ul>
         
          <button class="btn btn-outline-secondary rounded-0" :disabled="$v.$invalid">Kaydet</button><!--invalide baƒüladƒ±k ":"-->
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import './assets/bootstrap.min.css';
import {required,email,numeric,minLength,maxLength,sameAs,between} from "vuelidate/lib/validators"
export default {
  /*  Data fonksiyonu, formda kullanƒ±lan deƒüi≈ükenleri ve deƒüerlerini d√∂nd√ºr√ºr. data i√ßinde bulunanlar:
      email, password, repassword, age, selectedCategory: Formda girilen bilgilere baƒülanan deƒüi≈ükenlerdir.
      categories: Kategori se√ßeneklerini i√ßeren dizi.
      hobbies: Kullanƒ±cƒ±nƒ±n eklediƒüi hobilerin tutulduƒüu dizi.*/ 
  data() {
    return {
      email: null,
      password: null,
      repassword: null,
      age: null,
      selectedCategory: 'Yazƒ±lƒ±m',
      categories: ["Yazƒ±lƒ±m", "Donanƒ±m", "Cloud", "Sunucular", "Unix", "Linux", "Mac OS", "Windows"],
      hobbies: []
    };
  },
  //Bu b√∂l√ºmde, formdaki alanlara y√∂nelik doƒürulama kurallarƒ± tanƒ±mlanmƒ±≈ütƒ±r. Vuelidate k√ºt√ºphanesi ile yapƒ±lmƒ±≈ütƒ±r.
  validations : {
    /*  required: Bo≈ü bƒ±rakƒ±lmamalƒ±.
        email: Ge√ßerli bir e-posta formatƒ±nda olmalƒ±.
        uniq: Asenkron bir doƒürulama. Burada √∂rnek olarak bir e-posta kontrol√º yapƒ±lmƒ±≈ü 
              ve belirli bir e-posta ile e≈üle≈ümemesi saƒülanmƒ±≈ütƒ±r. */
    email : {
      required,
      email,
      /*uniq : value => {
        return value !== 'gokcenuryilmazz1@gmail.com' 
      }*/
     //asenkron i≈ülemler i√ßin
      uniq: value => {
        return new Promise((resolve) => {
          setTimeout(()=>{
            resolve(value !== 'gokcenuryilmazz1@gmail.com')
          },1000)
        })
      }
    },
    /*required: Zorunlu.
      numeric: ≈ûifre sadece rakamlardan olu≈ümalƒ±.
      minLength: En az 6 karakter olmalƒ±.
      maxLength: En fazla 8 karakter olmalƒ±. */
    password : {
      required,
      numeric,
      minLength : minLength(6) ,
      maxLength : maxLength(8)
    },
    //≈ûifre ile aynƒ± olmalƒ± (sameAs ile kontrol).
    repassword : {
      required,
      numeric,
      minLength : minLength(6) ,
      maxLength : maxLength(8),
      sameAs : sameAs('password')
      /* sameAs :  sameAs(vm =>{
          return vm.password + "87"})*/
    },
    //between(18, 60): Ya≈ü 18 ile 60 arasƒ±nda olmalƒ±.
    age : {
      required,
      numeric,
      between : between(18,60)
    },
    //checked: Sadece "Yazƒ±lƒ±m" kategorisi se√ßilebilir.
    selectedCategory : {
      //vm burada this g√∂revi g√∂r√ºyor
      checked(val,vm){
        return vm.selectedCategory === "Yazƒ±lƒ±m" ? true : false
      }
    },
    //minLength: En az 2 hobi girilmesi gerekli.
    //Her hobi en az 6 karakter uzunluƒüunda olmalƒ±.
    hobbies : {
      required,
      minLength: minLength(2),
      //burada $each ile arrayƒ±mƒ±n i√ßindeki elemenlerƒ± kontrol ediyorum
      $each : {
        value : {
          required,
          minLength : minLength(6)
        }
      }
    }
  },
  methods: {
    /*onSubmit(): Form g√∂nderildiƒüinde tetiklenen fonksiyondur. Formdaki bilgileri console.log ile ekrana yazdƒ±rƒ±r. */
    onSubmit() {
      let form = {
        email: this.email,
        password: this.password,
        category: this.category,
        hobbies: this.hobbies
      };
      console.log(form);
    },
    /*Yeni bir hobi eklemek i√ßin √ßaƒürƒ±lan fonksiyon. 
      Yeni hobi eklemek i√ßin bir nesne olu≈üturur ve hobbies dizisine ekler.*/
    newHobby() {
      let hobby = {
        id: Math.random() * Math.random() * 1000,
        value: ''
      };
      this.hobbies.push(hobby);
    }
  }
};
</script>
